.copyright__link {
  display: flex;
  align-items: center;
  justify-content: center;

  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: $color-primary;
  text-decoration: none;
  cursor: pointer;

  svg {
    margin-left: 15px;
  }
}

.copyright__link:hover svg {
  fill: $color-primary;
}

.copyright__link:active svg {
  fill: $color-gray-200;
}
